import React, { Component } from 'react';
import { Menu, Icon, Empty, Button, message, Pagination } from 'antd';
import './index.less';
import { service } from '../../../service/service';
import EstabPopup from '../../../components/establish-popup/establish-popup';

class NewActive extends Component {

  state = {
    categoryList: [],//
    parentKeys: ['all'],//
    showChildren: false,//
    childrenKeys: [],//
    childrenList: [],//
    gameList: [],//
    currentTypeId: '',//id
    currentLevel: '',//
    currentTypeName: '',//
    pageCount: 0,//
    pageNumber: 1,//
    pageSize: 10,//
    totalCount: 0,//
    showPopup: false,
    listHeight: '100%',
  }

  async componentWillMount() {
    // 
    let res = await service.queryAllGameType();
    console.log("", res);
    if (res.code === '1' && res.data.resultCode === '0000') {
      if (res.data.result && res.data.result.length > 0) {
        res.data.result.unshift({
          typeName: "",
          typeId: 'all',
          level: '',
          pageSize: this.state.pageSize,
          pageNumber: this.state.pageNumber
        });
        this.setState({ categoryList: res.data.result });
        // 
        this.queryGameList({
          typeId: '',
          level: '',
          pageSize: this.state.pageSize,
          pageNumber: this.state.pageNumber
        }, "");
      }
    } else {
      // message.error("")
    }
  }
  componentDidMount() {
    // jsgameList---
    window.onresize = () => {
      this.setState({
        listHeight: this.state.showChildren ? `${window.$(window).height() - 192}px` : `${window.$(window).height() - 144}px`
      })
    }
  }
  //
  onMouseEnter(type, list, event) {
    const selectKey = event.key;//typeIdantd
    const selectObj = list.filter((item, index) => item.typeId === selectKey)[0];//typeId
    if (type === '1') {
      // 
      const isShow = (selectObj.childrenList instanceof Array && selectObj.childrenList.length > 0) ? true : false;//childrenList
      this.setState({
        parentKeys: [event.key],
        childrenList: selectObj.childrenList,
        showChildren: isShow,
        currentTypeId: selectObj.typeId,
        currentLevel: selectObj.level,
        currentTypeName: selectObj.typeName,
        pageNumber: 1
      });
    } else {
      // 
      this.setState({
        childrenKeys: [event.key],
        currentTypeId: selectObj.typeId,
        currentLevel: selectObj.level,
        currentTypeName: selectObj.typeName,
        pageNumber: 1
      })
    }
    let { level, typeId, typeName } = selectObj;
    let data = null;
    //leveltypeId
    if (typeId === 'all') {
      data = {
        pageSize: this.state.pageSize,
        pageNumber: 1
      }
    } else {
      data = {
        level: level,
        typeId: typeId,
        pageSize: this.state.pageSize,
        pageNumber: 1
      }
    }
    this.queryGameList(data, selectObj.typeName);
  }

  //  
  async queryGameList(params, typeName) {
    let res = await service.queryGameList(params);
    console.log(typeName + "", res);
    if (res.code === '1' && res.data.resultCode === '0000') {
      if (res.data.result && res.data.result.gameVoList && res.data.result.gameVoList.length > 0) {
        // jsgameList---
        let height = this.state.showChildren ? `${window.$(window).height() - 192}px` : `${window.$(window).height() - 144}px`;
        this.setState({
          gameList: res.data.result.gameVoList,
          pageCount: res.data.result.pageCount,
          totalCount: res.data.result.totalCount,
          listHeight: height
        })
      } else {
        // message.warning(`${typeName}`);
        this.setState({
          gameList: []
        })
      }
    } else {
      this.setState({
        gameList: []
      })
      // message.error(`${typeName}`);
    }
  }

  // 
  handlepPageChange(page, pageSize) {
    this.setState({
      pageNumber: page,
    }, () => {
      this.queryGameList({
        level: this.state.currentLevel,
        typeId: this.state.currentTypeId,
        pageSize: this.state.pageSize,
        pageNumber: page,
      }, this.state.currentTypeName)
    })
  }
  // 
  handlepPageSizeChange(current, size) {
    this.setState({
      pageNumber: 1,
      pageSize: size
    }, () => {
      this.queryGameList({
        level: this.state.currentLevel,
        typeId: this.state.currentTypeId,
        pageSize: size,
        pageNumber: 1,
      }, this.state.currentTypeName)
    })
  }

  createGame(...args) {
    this.setState({
      gameId: args[0],
      showPopup: true
    })
  }
  render() {
    return (
      <div className="newActive">
        {this.state.showPopup && <EstabPopup gameId={this.state.gameId} handleClose={() => { this.setState({ showPopup: false }) }} />}
        <div ref='navmenu'>
          {/*  */}
          <Menu selectedKeys={this.state.parentKeys} className="pl80" mode="horizontal">
            {
              this.state.categoryList.map((item, index) => {
                return <Menu.Item onMouseEnter={this.onMouseEnter.bind(this, '1', this.state.categoryList)} key={item.typeId}>
                  <span className="category-one">{item.typeName}</span>
                  {item.childrenList ? (this.state.parentKeys[0] === item.typeId ? <Icon type='up' /> : <Icon type='down' />) : ('')}
                </Menu.Item>
              })
            }
          </Menu>
          {/*  */}
          {
            this.state.showChildren &&
            <Menu selectedKeys={this.state.childrenKeys} className="pl80 resetBg" mode="horizontal">
              {
                this.state.childrenList.map((item, index) => {
                  return <Menu.Item onMouseEnter={this.onMouseEnter.bind(this, '2', this.state.childrenList)} key={item.typeId}>
                    <span className="category-two">{item.typeName}</span>
                  </Menu.Item>
                })
              }
            </Menu>
          }
        </div>
        {/*  */}
        <div className="gameList" style={{ height: this.state.listHeight }}>
          {
            this.state.gameList.length > 0 ?
              (
                <div className="game-content">
                  {
                    this.state.gameList.map((item, index) => {
                      return <div className="detail" key={index}>
                        <img className="game-img" src={item.imageStatic} alt="" />
                        <img className="game-gif" src={item.imageGif} alt="" />
                        <div className="botttom">
                          <div className="name">{item.gameName}</div>
                          <div className="create-btn">
                            <Button type="primary" onClick={this.createGame.bind(this, item.gameId)}></Button>
                          </div>
                        </div>
                      </div>
                    })
                  }
                </div>
              ) :
              (<Empty />)
          }
        </div>
        {/*  */}
        <div className="pagination">
          <Pagination
            pageSizeOptions={['5', '10', '15', '20', '25', '30']}
            total={this.state.totalCount}
            pageSize={this.state.pageSize}
            showSizeChanger={true}
            showQuickJumper={true}
            onChange={this.handlepPageChange.bind(this)}
            onShowSizeChange={this.handlepPageSizeChange.bind(this)}
          />
        </div>
      </div>
    )
  }
}

export default NewActive
