import React, { Component } from 'react';
import './index.less';
import { Table, Divider, message, Modal } from 'antd';
import { service } from '../../../service/service';
import Popup from '../../../components/preview-popup/preview-popup';
import { createHashHistory } from 'history';
import copy from 'copy-to-clipboard';
import QRCode from 'qrcode.react';
import { utils } from '../../../common/Utils';

class MyActive extends Component {

  state = {
    data: [],
    total: 0,
    showQuickJumper: true,
    showSizeChanger: true,
    defaultCurrent: 1,
    defaultPageSize: 2,
    pageSizeOptions: ['1', '2', '3', '4', '5', '6'],
    onChange: this.handlepPageChange.bind(this),
    onShowSizeChange: this.handlepPageSizeChange.bind(this),
    showPopup: false,//
    loading: false,//loading
    record: null,//
    gameIndexUrl: '',//
    visible: false,//
    showQrCode: false,//
    qrCodeLeft: '',//left
    qrCodeTop: '',//top
    qrCodeLink: '',//
    visibleDelete: false,
    deleteTips: '',
    thirdActivityCode: '',
    activityCode: ''
  }
  columns = [
    {
      title: '',
      key: 'id',
      width: 300,
      render: (text, record) => {
        return (
          <div className="act-name">
            <div onMouseOver={(e) => this.onMouseOver(e, record)} onMouseOut={(e) => this.onMouseOut(e)} className="icon-qrcode"></div>
            <img className="act-simg" src={record.imageUrl} alt="" />
            <div className="act-title">{record.activityName}</div>
          </div>
        )
      },
    },
    {
      title: '',
      dataIndex: 'activityCode',
      width:240
    },
    {
      title: '',
      width: 160,
      render: (text, record) => (
        <div>
          <div>{utils.formateDate(record.beginTime)}</div>
          <div>{utils.formateDate(record.endTime)}</div>
        </div>
      ),
    },
    {
      title: '',
      width: 88,
      render: (text, record) => {
        let showText = {
          '1': '',
          '2': '',
          '3': '',
          '4': '',
          '5': ''
        }[String(record.activityStatus)];
        return (
          <span>{showText}</span>
        )
      }
    },
    {
      title: '',
      width: 400,
      render: (text, record) => {
        return (
          <span>
            {/* <a onClick={this.release.bind(this, record)}></a> */}
            {/* <Divider type="vertical" /> */}
            <a onClick={this.edit.bind(this, record)}></a>
            <Divider type="vertical" />
            <a onClick={this.preview.bind(this, record)}></a>
            <Divider type="vertical" />
            <a onClick={this.delete.bind(this, record)}></a>
            <Divider type="vertical" />
            <a onClick={this.copy.bind(this, record)}></a>
          </span>
        )
      },
    },
  ];

  componentWillMount() {
    this.queryActivityList({
      pageSize: 10,
      pageNumber: 1
    })
  }

  async queryActivityList(data) {
    let res = await service.queryActivityList(data);
    console.log("", res);
    if (res.code == '1' && res.data.resultCode == '0000') {
      this.setState({
        data: res.data.result.activityVoList,
        total: res.data.result.totalCount
      })
    } else {
      // message.error("")
    }
  }

  // 
  handlepPageChange(page, pageSize) {
    this.queryActivityList({
      pageSize: pageSize,
      pageNumber: page
    })
  }

  // 
  handlepPageSizeChange(current, size) {
    this.queryActivityList({
      pageSize: size,
      pageNumber: 1
    })
  }
  onMouseOver(...args) {
    let top = '';
    let left = document.getElementsByClassName('act-table')[0].getBoundingClientRect().left;
    let clientRect = args[0].currentTarget.getBoundingClientRect();
    let bottomClientY = document.body.clientHeight - clientRect.bottom;
    if (bottomClientY > 216) {
      top = clientRect.bottom + 6;
    } else {
      top = clientRect.top - 216;
    }
    this.setState({
      showQrCode: true,
      qrCodeLeft: `${left}px`,
      qrCodeTop: `${top}px`,
      gameIndexUrl: args[1].gameIndexUrl
    })
  }
  onMouseOut() {
    this.setState({
      showQrCode: false,
      qrCodeLeft: '',
      qrCodeTop: ''
    })
  }
  // 
  release(...args) {
    console.log("", args[0]);
  }
  // 
  edit(...args) {
    const history = createHashHistory();
    history.push(`/Edit?gameId=${args[0].gameId}&activityCode=${args[0].activityCode}&activityUrlType=3`);

  }
  // 
  preview(...args) {
    this.setState({
      record: args[0],
      showPopup: true
    })
  }
  // 
  delete(...args) {
    console.log("", args[0]);
    this.setState({
      visibleDelete: true,
      deleteTips: `${args[0].activityName}`,
      thirdActivityCode: args[0].thirdActivityCode,
      activityCode: args[0].activityCode
    })
    // Modal.confirm({
    //   title: '',
    //   content: `${args[0].activityName}`,
    //   okText: '',
    //   okType: 'danger',
    //   cancelText: '',
    //   onOk: async (e) => {
    //     let res = await service.delActivity({
    //       thirdActivityCode: args[0].thirdActivityCode,
    //       activityCode: args[0].activityCode
    //     });
    //     if (res.code == '1' && res.data.resultCode == '0000') {
    //       // message.success("");
    //       this.queryActivityList();
    //     } else {
    //       // message.error("");
    //     }
    //   }
    // });
  }
  // 
  async confirmDelete() {
    let res = await service.delActivity({
      thirdActivityCode: this.state.thirdActivityCode,
      activityCode: this.state.activityCode
    });
    if (res.code == '1' && res.data.resultCode == '0000') {
      this.queryActivityList();
    }
    this.closeDelete();
  }
  // 
  closeDelete() {
    this.setState({
      visibleDelete: false
    })
  }
  // 
  copy(...args) {
    if (copy(args[0].gameIndexUrl)) {
      message.success("!");
    } else {
      this.setState({
        gameIndexUrl: args[0].gameIndexUrl,
        visible: true
      })
    }
  }
  //
  closeCopy(...args) {
    this.setState({
      visible: false
    })
  }
  render() {
    return (
      < div className="myActive" id="myActive">
        {this.state.showPopup && <Popup record={this.state.record} handleClose={() => { this.setState({ showPopup: false }) }} />}
        <div className="myact-title">
          
        </div>
        <Table
          className="act-table"
          scroll={{ y: '700px' }}
          rowKey={record => record.activityCode}
          dataSource={this.state.data}
          columns={this.columns}
          pagination={{
            showQuickJumper: true,
            showSizeChanger: true,
            defaultCurrent: 1,
            defaultPageSize: 10,
            total: this.state.total,
            pageSizeOptions: ['5', '10', '15', '20', '25', '30'],
            onChange: this.handlepPageChange.bind(this),
            onShowSizeChange: this.handlepPageSizeChange.bind(this)
          }}
          loading={this.state.loading}
        />
        <Modal
          title=""
          visible={this.state.visible}
          bodyStyle={{ height: 'auto' }}
          onOk={this.closeCopy.bind(this)}
          onCancel={this.closeCopy.bind(this)}
        >
          <p></p>
          <p>{this.state.gameIndexUrl}</p>
        </Modal>
        <Modal
          title=""
          visible={this.state.visibleDelete}
          okType='danger'
          width={400}
          onOk={this.confirmDelete.bind(this)}
          onCancel={this.closeDelete.bind(this)}
        >
          <p>{this.state.deleteTips}</p>
        </Modal>
        {this.state.showQrCode &&
          <div className="qrCode" style={{ left: this.state.qrCodeLeft, top: this.state.qrCodeTop }}>
            <QRCode value={this.state.gameIndexUrl} size={144} />
            <p>
              <i></i>
              <span></span>
            </p>
          </div>}
      </div >
    );
  }
}

export default MyActive